<template>
  <component :is="currentComponent" />
</template>

<script>
import { defineAsyncComponent, ref, onMounted, onUnmounted, markRaw } from 'vue'

export default {
  name: 'HomeView',
  setup() {
    const isMobile = ref(false)
    
    const MobileLanding = markRaw(defineAsyncComponent(() => 
      import('@/components/MobileLanding.vue')
    ))
    
    const Landing = markRaw(defineAsyncComponent(() => 
      import('@/components/Landing.vue')
    ))
    
    const currentComponent = ref(Landing)

    const checkDevice = () => {
      isMobile.value = window.innerWidth <= 768
      currentComponent.value = isMobile.value ? MobileLanding : Landing
    }

    onMounted(() => {
      checkDevice()
      window.addEventListener('resize', checkDevice)
    })

    onUnmounted(() => {
      window.removeEventListener('resize', checkDevice)
    })

    return {
      currentComponent
    }
  }
}
</script> 